.background1 {
    background-image: url(/src/assets/img/hero-home.jpg);
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.background1 div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin: 0rem 3rem;
}

.intro-text {
    max-width: 30rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 100;
    font-size: 2rem;
    color: rgb(255, 255, 255);
}

.intro-btn {
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
    background-color: white;
    color: black;
    padding: .5rem 1.5rem;
    font-size: 1.1rem;
    transition: all .4s ease-in-out;
}

.intro-btn:hover {
    background-color: black;
    color: white;
}

@media (max-width: 400px) {
    .intro-text {
        text-align: center;
        letter-spacing: .5rem;
        font-size: 2rem;
        padding: 0;
        margin: 0;
        margin-top: 8rem;
        margin-left: .4rem;
        color: white;
    }

    .shop-btn {
        border: 2px solid rgb(255, 255, 255);
        text-transform: uppercase;
        letter-spacing: .3rem;
        cursor: pointer;
        margin-left: 0rem;
        margin-top: 8rem;
        padding: 0;
        outline: none;
    }
}

/* . . . */

.special-background {
    height: 220vh;
    position: relative;
    margin: 7rem 0rem;
    background-color: white;
}

.special-backgroundM {
    display: none;
    background-color: white;
}

.special-background h1 {
    font-family: 'Poppins', sans-serif;
    font-weight: 100;
    display: flex;
    justify-content: center;
    font-size: 2.5rem;
}

.special-box {
    display: flex;
    justify-content: center;
    align-items: center;
}

.zoom img {
    height: 40rem;
    transition: transform 1s ease-in-out;
}

.item-img1 {
    width: 25rem;
}

.item-img4 {
    width: 25rem;
}

.zoom img:hover {
    transform: scale(1.1);
}

.zoom {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.zoom-item1 {
    height: 35rem;
    width: 22rem;
    position: absolute;
    top: 18%;
    left: 12%;
}

.zoom-item2 {
    position: absolute;
    top: 5%;
    right: 12%;
}

.zoom-item3 {
    position: absolute;
    bottom: 0%;
    right: 50%;
}

.zoom-item4 {
    height: 35rem;
    width: 22rem;
    position: absolute;
    bottom: 16%;
    right: 15%;
}

.zoom .item1 {
    overflow: hidden;
    margin: 1rem;
}

.zoom .item2 {
    overflow: hidden;
    margin: 1rem;
}

.zoom .item3 {
    overflow: hidden;
    margin: 1rem;
}

.zoom .item4 {
    overflow: hidden;
    margin: 1rem;
}

.btnall-box {
    position: absolute;
    left: -3rem;
}

.btnall-box1 p {
    font-size: 1.2rem;
}

.btnall-box2 p {
    font-size: 2rem;
}

.btnall-box3 p {
    font-size: 2rem;
}

.btnall-box4 p {
    font-size: 1.2rem;
}

.btnall-box p {
    font-weight: 900;
}

.btnall {
    font-size: .8rem;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: .1rem;
    color: black;
}

.btnall:hover {
    text-decoration: underline;
}


@media (max-width: 1300px) {

    .special-background {
        height: auto;
        padding: 6rem 0rem;
    }

    .special-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .allSpecialItems {
        width: 30rem;
    }

    .zoom {
        margin: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .zoom-item1 {
        height: auto;
        width: auto;
        position: static;
    }

    .zoom-item2 {
        position: static;
    }

    .zoom-item3 {
        position: static;
    }

    .zoom-item4 {
        height: auto;
        width: auto;
        position: static;
    }

    .btnall-box {
        position: static;
    }
}

@media (max-width: 600px) {

    .special-background {
        height: auto;
        padding: 6rem 0rem;
    }

    .special-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .allSpecialItems {
        width: 24rem;
    }
}

/* Collection */

.Collection-box {
    margin-bottom: 10rem;
}

.collection-info-box {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.collection-info-box h4 {
    font-family: 'Poppins', sans-serif;
    font-weight: 100;
    font-size: 2.8rem;
}

.collection-title-box {
    display: flex;
    justify-content: center;
}

.collection-title-box p {
    color: rgb(165, 165, 165);
    margin: 0rem 2rem;
    cursor: pointer;
}

.collection-img-box {
    margin: 0rem 10rem;
}

.collection-img-box a {
    font-size: .8rem;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: .1rem;
    color: black;
}

.collection-img-box a:hover {
    text-decoration: underline;
}

.collection-img-box div {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding: 0rem 0rem;
}

.collection-img-box div div {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.collection-img-box div a img {
    height: 32rem;
}


@media (max-width: 1300px) {

    .collection-title-box {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        align-content: center;
        align-items: center;
        align-self: center;
    }

    .collection-title-box p {
        display: flex;
        justify-content: center;
    }
}